.instructions {
    position: fixed;
    overflow: hidden;
    top: 25%;
    left: 20%;
    width: 60%;
    height: 60%;
    color: white;
    background-color: rgba(0, 0, 0, 0.9);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0.5rem 0.5rem 0.5rem #3a3a3a;
    z-index: 1000;
    animation: slideInLeft 0.6s forwards;
}

@keyframes slideInLeft {
    0% {
        transform: translateX(-900px);
      }
      100% {
        transform: translateX(0);
      }
  }

.hidden {
    display: none;
}

.button {
    width: 40%;
    background-color: #ccc;
    color: black;
    padding: 1rem 2rem;
    margin: 1rem 0.5rem;
    border: none;
    border-radius: 0.5rem;;
    cursor: pointer;
    display: block;
}

.ul {
    line-height: 3rem;
    padding: 0 7% 0 15%;
    
}

@media screen and (max-width: 1180px) {
    .ul {
        line-height: 3rem;
        padding: 0 7% 0 15%;
        overflow: scroll;
    }
}

